.app__menuitem{
    width: 100%;
    margin: 1rem 0;

    display: flex;
    flex-direction: column;
}
.app__menuitem-head{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.app__menuitems-sub{
    width: 100%;
    margin-top: 1.2rem;
}

.app__menuitem-name{
    flex: 1;


}
.app__menuitem-dash{
    width: 85px;
    height: 1px;

    background: var(--color-white);
    margin-bottom: -20px;
    margin-left:10px ;
}
.app__menuitem-price{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;

}
